.theme-panel {
  &__body {
    background-color: rgb(0, 255, 251);
    width: 200px;
    height: 200px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 20px rgba(0, 0, 0, .2);
  }
  & {
    visibility: visible;
    opacity: 1;
    transition: all .1s linear;;
  }
  &--hidden {
    visibility: hidden;
    opacity: 0;
  }
}
