.loading {
  & {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: $color-light;
    z-index: $z-index-loading;
    visibility: visible;
    opacity: 1;
    transition: all .4s ease-in;
    color: $color-purple;
    font-weight: 500;
  }
  &--hidden {
    visibility: hidden;
    opacity: 0;
  }
}

